@font-face {
  font-family: Regular;
  src: url(./fonts/Amaranth-Regular.ttf);
}

@font-face {
  font-family: Bold;
  src: url(./fonts/Amaranth-Bold.ttf);
}

* {
  font-family: Regular;
}

.no-drag {
  -webkit-user-drag: none;
  user-select: none;
}

.style {
  padding-top: 65px;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 768px) {
  .style {
    height: fit-content;
    margin-bottom: 50px;
  }
}
.hover {
  box-shadow: 0px 1px 5px 0px grey;
  transition: box-shadow 0.5s;
}
.hover:hover {
  box-shadow: 3px 3px 5px 0px gray;
}
